/*

*/
b.shock b {
  width: 55%;
  height: 100%;
  position: relative;
  box-sizing: border-box;
  margin: 0 auto;
  border-style: solid;
  border-color: #999;
  border-width: 3px 1px;
  border-radius: 10%;
  animation: shock 1s infinite;
}
b.shock b i {
  border-radius: 20%;
  background-color: #999;
  width: 2px;
  height: 50%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
b.shock b i:nth-child(1) {
  left: -35%;
}
b.shock b i:nth-child(2) {
  right: -35%;
}
b.shock b i::before {
  content: '';
  border-radius: 10%;
  width: 2px;
  height: 60%;
  background-color: #999;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
b.shock b i:nth-child(1)::before {
  left: -200%;
}
b.shock b i:nth-child(2)::before {
  right: -200%;
}
@keyframes shock {
from {
    transform: none;
}
15% {
    transform: translate3d(-25%, 0, 0) rotate3d(0, 0, 1, -18deg);
}
30% {
    transform: translate3d(20%, 0, 0) rotate3d(0, 0, 1, 15deg);
}
45% {
    transform: translate3d(-15%, 0, 0) rotate3d(0, 0, 1, -13deg);
}
60% {
    transform: translate3d(10%, 0, 0) rotate3d(0, 0, 1, 12deg);
}
75% {
    transform: translate3d(-5%, 0, 0) rotate3d(0, 0, 1, -1deg);
}
to {
    transform: none;
}
}



