
.flex-around-end {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: end;
      align-items: flex-end;
}
.flex-center {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.iconbg {
  background-color: #999;
}
b.photo {
  position: relative;
  box-sizing: border-box;
  overflow: hidden;
  border: 1px solid #999;
  border-radius: 10%;
}
b.photo::before {
  content: "";
  box-sizing: border-box;
  position: absolute;
  top: 10%;
  right: 10%;
  width: 20%;
  height: 20%;
  border-radius: 50%;
  border: 1px solid #999;
}
b.photo b.photo-box {
  width: 200%;
  height: 200%;
  position: absolute;
  top: 35%;
  left: 0;
  animation: photo 2s linear infinite;
}
b.photo b.photo-box i {
  position: absolute;
  border-radius: 10%;
  width: 50%;
  height: 50%;
  background-color: #999;
}
b.photo b.photo-box i:nth-child(1),
b.photo b.photo-box i:nth-child(3) {
  transform: translate(-15%, 20%) rotateZ(45deg);
}
b.photo b.photo-box i:nth-child(2),
b.photo b.photo-box i:nth-child(4) {
  transform: translate(45%, 40%) rotateZ(30deg);
}
b.photo b.photo-box i:nth-child(3),
b.photo b.photo-box i:nth-child(4) {
  left: 50%;
}
@keyframes photo {
0% {
    transform: translate(0%);
}
100% {
    transform: translate(-50%);
}
}



