b.refresh {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
b.refresh > b {
  position: relative;
  width: 25%;
  height: 25%;
  border-radius: 50%;
}
b.refresh > b > i {
  position: absolute;
  opacity: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #999;
}
b.refresh > b > i:nth-child(1) {
  animation: refresh 1s infinite 0.12s linear;
}
b.refresh > b > i:nth-child(2) {
  animation: refresh 1s infinite 0.24s linear;
}
b.refresh > b > i:nth-child(3) {
  animation: refresh 1s infinite 0.36s linear;
}
b.refresh > b > i:nth-child(4) {
  animation: refresh 1s infinite 0.48s linear;
}
b.refresh > b > i:nth-child(5) {
  animation: refresh 1s infinite 0.6s linear;
}
b.refresh > b > i:nth-child(6) {
  animation: refresh 1s infinite 0.72s linear;
}
b.refresh > b > i:nth-child(7) {
  animation: refresh 1s infinite 0.84s linear;
}
b.refresh > b > i:nth-child(8) {
  animation: refresh 1s infinite 0.96s linear;
}
b.refresh > b > i:nth-child(1) {
  transform: translate(150%);
}
b.refresh > b > i:nth-child(2) {
  transform: translate(110%, 110%);
}
b.refresh > b > i:nth-child(3) {
  transform: translate(0, 150%);
}
b.refresh > b > i:nth-child(4) {
  transform: translate(-110%, 110%);
}
b.refresh > b > i:nth-child(5) {
  transform: translate(-150%);
}
b.refresh > b > i:nth-child(6) {
  transform: translate(-110%, -110%);
}
b.refresh > b > i:nth-child(7) {
  transform: translate(0, -150%);
}
b.refresh > b > i:nth-child(8) {
  transform: translate(110%, -110%);
}
@keyframes refresh {
0% {
    opacity: 1;
}
50% {
    opacity: 0.1;
}
100% {
    opacity: 0;
}
}



