b.setting {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}
b.setting::before,
b.setting::after {
  content: "";
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
b.setting::before {
  width: 75%;
  height: 75%;
  background-color: #999;
}
b.setting::after {
  width: 40%;
  height: 40%;
  background-color: #fff;
}
b.setting > b {
  position: relative;
  width: 20%;
  height: 100%;
  animation: rotating 4s infinite linear;
}
b.setting > b i {
  position: absolute;
  width: 100%;
  height: 100%;
  transform-origin: center center;
  border-radius: 2px;
  background-color: #999;
}
b.setting > b i:nth-child(0) {
  transform: rotate(0deg);
}
b.setting > b i:nth-child(1) {
  transform: rotate(45deg);
}
b.setting > b i:nth-child(2) {
  transform: rotate(90deg);
}
b.setting > b i:nth-child(3) {
  transform: rotate(135deg);
}
@keyframes rotating {
0% {
    transform: rotate(0deg);
}
100% {
    transform: rotate(360deg);
}
}



