/*

 */
b.delete > i {
  background-color: #999;
}
b.delete > i:nth-child(1) {
  margin: 5px auto 1px;
  width: 80%;
  height: 2px;
  animation: updown 3s ease infinite;
  transform-origin: -10px center;
}
b.delete > i:nth-child(1)::before {
  content: "";
  width: 25%;
  height: 3px;
  border-radius: 3px;
  border: 2px solid #999;
  position: absolute;
  top: -5px;
  transform: translate(-50%);
}
b.delete > i:nth-child(2) {
  position: relative;
  width: 60%;
  height: 70%;
  box-sizing: border-box;
  margin: 0 auto;
  background-color: #fff;
  border: 2px solid #999;
  border-top: transparent;
  border-radius: 0 0 5px 5px;
}
b.delete > i:nth-child(2)::before,
b.delete > i:nth-child(2)::after {
  content: "";
  position: absolute;
  width: 2px;
  height: 40%;
  top: 5px;
  background-color: #999;
}
b.delete > i:nth-child(2)::before {
  left: 15%;
}
b.delete > i:nth-child(2)::after {
  right: 15%;
}
@keyframes updown {
0% {
    transform: rotate(0deg);
}
30% {
    transform: rotate(-15deg);
}
60% {
    transform: rotate(0deg);
}
100% {
    transform: rotate(0deg);
}
}



