b.ripples {
  position: relative;
}
b.ripples > i {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #999;
  position: absolute;
  opacity: 0;
  animation: ripples 1.5s linear infinite;
}
b.ripples > i:nth-child(1) {
  animation-delay: 0s;
}
b.ripples > i:nth-child(2) {
  animation-delay: 0.2s;
}
b.ripples > i:nth-child(3) {
  animation-delay: 0.4s;
}
@keyframes ripples {
0% {
    transform: scale(0);
    opacity: 0;
}
50% {
    transform: scale(0.3);
    opacity: 1;
}
100% {
    transform: scale(1);
    opacity: 0;
}
}



